import React, { Component } from "react";
import Iframe from 'react-iframe';
import ReactPlayer from 'react-player'
import NetworkCardComponent from '../../networkCardComponent'
import AuthorDetails from './../../sepcomponents/AuthorDetails'
import FontFamilyForPost from './../../sepcomponents/FontFamilyForPost'
import { API_ROOT_URL, CloudUrl, FB_CLIENT_ID, INSTAGRAM_IMAGE_BLANK, NO_CAMERA_IMAGE, V_EVENT } from './../../../constants'
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT, IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP, findNetworkDataFromWebFilters } from '../../../utils'
import parse from 'html-react-parser';


class Posts extends Component {
    state = {
        id: this.props.activeData.id,
    }
    componentDidMount() {
        const { mediaClass, activeData } = this.props;
        this.setState({ mediaClass, id: activeData.id }, () => this.onZoomStyleLoad())
    }
    onZoomStyleLoad = () => {
        const { mediaClass } = this.state;
        const { newApi } = this.props;
        this.setState({ fade: "fade-in" })
        setTimeout(() => {
            if (mediaClass === 'makeBgImg') this.setState({ mediaClass: `${mediaClass} ${newApi.wall.Personalization.signageTheme == 21 ? ' zoom2x' : ''}` })
        }, 3000)
    }
    componentDidUpdate(prevProps, prevState) {
        if (prevProps.activeData.id !== prevState.id) {
            const { mediaClass, activeData } = prevProps;
            this.setState({ mediaClass, id: activeData.id, fade: "", active: "" }, () => this.onZoomStyleLoad());
        }
    }
    addNetwork = (name) => {
        let scrlBarComan = document.querySelector(".scrlBarComan")
        if (scrlBarComan) scrlBarComan.setAttribute("data-network", name)
    }


    render() {

        const { activeData, typeClass, blurStyle, blurClass, columnLeftClass, columnRightClass, newApi, getVideoDuration, getVideoProgressDuration, activeIndex, active, updateNewImage } = this.props;
        const { mediaClass } = this.state;

        const fbConfig = {
            file: {
                attributes: {
                    autoPlay: true,
                    muted: true
                }
            },
            facebook: {
                appId: FB_CLIENT_ID
            }
        }

        const netwrokd = findNetworkDataFromWebFilters(activeData.networkId, newApi.webFilters);
        return (
            <>
                <div id={`"postId${activeData.id}`} className={`feedId${activeData.feedId} postItem item ${typeClass} flatCard animated in autoBold appliedchLimit ${active}`} >





                    {newApi.wall.Personalization.signageTheme == 37 ? <div className="blur-bg tb_slide_bg_color__" style={V_EVENT ? {} : { background: newApi.wall.ThemeRule.transparent === 0 ? newApi.wall.ThemeRule.backgroundColor == "" ? activeData.networkId == 2 ? 'linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%)' : netwrokd.color : newApi.wall.ThemeRule.backgroundColor : activeData.networkId == 2 ? 'linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%)' : netwrokd.color }} ></div> : null}

                    {newApi.wall.Personalization.signageTheme == 21 ? <div className="blur-bg tb_slide_bg_img_full__" style={blurStyle}></div> : null}



                    <div className="post">
                        <div className={columnLeftClass} style={(activeData.networkId === 2 || activeData.networkId === 18) ? { textAlign: 'center' } : {}}>
                            {(activeData.type === 2 || activeData.type === 4) && activeData.file && <>
                                {
                                    ((activeData.networkId === 2 || activeData.networkId === 18)) && <>
                                        <div className={` blur-bg tb_slide_bg_img__ ${blurClass}`} style={blurStyle}></div>

                                        <img src={activeData.postFileNew} className={mediaClass} data-link={activeData.link} data-load="0" data-network={activeData.networkId} data-postid={activeData.id}
                                            data-wall-id={newApi.wall.Wall.id} data-owner-id={newApi.wall.Wall.owner} data-item-id={activeData.id}
                                            data-tb={newApi.wall.UserDetail.db_table}
                                            data-stories={activeData.stories}
                                            data-filter-id={activeData.filterId}
                                            onLoad={updateNewImage}
                                            onError={(e) => IMG_LOAD_AFTER_COMPLETE_LAYOUT(e)}
                                        />
                                    </>}
                                {((activeData.networkId !== 2 && activeData.networkId !== 18)) && <>
                                    <div className={`blur-bg tb_slide_bg_img__ ${blurClass}`} style={blurStyle}></div>
                                    <div className={mediaClass} style={{ backgroundImage: `url(${activeData.postFileNew})` }} >
                                        {activeData.embed ? parse(activeData.embed) : <></>}
                                    </div>
                                </>}
                            </>}

                            {(activeData.type === 3 || activeData.type === 5) && <>

                                <div className={`blur-bg tb_slide_bg_img_vid__ ${blurClass}`} style={blurStyle}></div>
                                <div className={mediaClass} style={blurStyle}>


                                    {
                                        (activeData.type === 3 || activeData.type === 5) && <>
                                            {
                                                activeData.link && (activeData.link.indexOf("facebook") >= 0 || activeData.link.indexOf("instagram") >= 0 || activeData.link.indexOf("youtube") >= 0 || activeData.link.indexOf("youtu.be") >= 0 || activeData.link.indexOf("vimeo") >= 0)  &&

                                                <ReactPlayer url={`${activeData.mediaUrl}`} height='100%' width='100%' controls={true} allowFullScreen={true} onProgress={getVideoProgressDuration} onDuration={getVideoDuration} playing muted config={fbConfig} />
                                            }
                                            {

                                                activeData.link && (activeData.link.indexOf("soundcloud") >= 0) &&
                                                <Iframe
                                                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                                    src={`${activeData.mediaUrl}`}
                                                    allowFullScreen="true"
                                                    style={{ width: "100%!important" }}
                                                ></Iframe>

                                            }

                                            {
                                                activeData.link && ((activeData.networkId && activeData.networkId == 30) && activeData.link.indexOf("vk") >= 0) &&
                                                <Iframe
                                                    className="tb-detail-image-iframe"
                                                    frameBorder="0"
                                                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                                    src={`${activeData.mediaUrl}&autoplay=1&mute=1`}
                                                    allowFullScreen="true"
                                                    style={{ width: "100%!important" }}
                                                    data-filter-id={activeData.filterId}
                                                    data-stories={activeData.stories}
                                                    data-type="video"
                                                    data-network={activeData.networkId}
                                                    data-link={activeData.link}
                                                    data-activeData-id={activeData.id}
                                                    data-load={0}
                                                ></Iframe>

                                            }

                                            {
                                                (activeData.link || activeData.networkId == 29 || activeData.networkId == 28) && (activeData.mediaUrl.indexOf("onsiteupload/video") >= 0 || activeData.link.indexOf("linkedin") >= 0 || activeData.link.indexOf("twitter") >= 0 || activeData.link.indexOf("tumblr") >= 0 || activeData.link.indexOf("tiktok") >= 0) && <video
                                                    onDuration={getVideoDuration}
                                                    onProgress={getVideoProgressDuration}
                                                    src={`${activeData.mediaClip && String(activeData.mediaClip).length > 0 ? activeData.mediaClip : activeData.mediaUrl}`}
                                                    autoPlay={true}
                                                    muted={true}
                                                    onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP(e) }}
                                                    data-type="video"
                                                    data-stories={activeData.stories}
                                                    data-network={activeData.networkId}
                                                    data-filter-id={activeData.filterId}
                                                    data-postid={activeData.id}
                                                    data-link={activeData.link}
                                                    data-load={0}
                                                    data-tb={newApi.wall.UserDetail.db_table}
                                                    data-wall-id={newApi.wall.Wall.id} data-owner-id={newApi.wall.Wall.owner} data-item-id={activeData.id}
                                                ></video>
                                            }
                                        </>
                                    }
                                </div>
                            </>}
                        </div>

                        <div className={columnRightClass}>
                            <div className="postContentCard" style={{ backgroundColor: newApi.wall.ThemeRule.cardColor }}>
                                <div className="postContentClasic">
                                    {netwrokd.name != null ? netwrokd.id === 20 || (newApi.wall.ThemeRule.iconType === 1 && newApi.wall.Personalization.signageTheme == 21) ? <div className="postNetwork" data-network={netwrokd.name} data-network-color={netwrokd.color}>
                                        <a target="_blank">
                                            {netwrokd.icon == "fa-twitter" ? <svg id="twitter" xmlns="http://www.w3.org/2000/svg" width="50" height="50" viewBox="0 0 44.518 44.518"><path id="Path_5" data-name="Path 5" d="M0,0H44.518V44.518H0Z" fill="none" />
                                                <path id="Path_7" data-name="Path 7" d="M19.036,13.554,30.942,0H28.121L17.778,11.766,9.523,0H0L12.486,17.794,0,32.006H2.821L13.737,19.578l8.72,12.429H31.98Zm-3.865,4.4L13.9,16.177,3.838,2.083H8.172L16.3,13.462l1.262,1.773L28.12,30.025H23.786Zm0,0" transform="translate(6.313 6.281)" fill={newApi.wall.ThemeRule.iconType == 1 ? newApi.wall.ThemeRule.iconColor : null} />
                                            </svg> : <i className={netwrokd.id == 20 ? `tb_icon fa ${netwrokd.icon}` : `fa ${netwrokd.icon}`} style={{ color: newApi.wall.ThemeRule.iconType == 1 ? newApi.wall.ThemeRule.iconColor : null, fontSize: "50px" }}></i>}

                                        </a>
                                    </div> :
                                        <div className="postNetwork">
                                            {activeData.networkId == 1 ? <svg id="twitter" xmlns="http://www.w3.org/2000/svg" width="50" height="50" viewBox="0 0 44.518 44.518"> <path id="Path_5" data-name="Path 5" d="M0,0H44.518V44.518H0Z" fill="none" />
                                                <path id="Path_7" data-name="Path 7" d="M19.036,13.554,30.942,0H28.121L17.778,11.766,9.523,0H0L12.486,17.794,0,32.006H2.821L13.737,19.578l8.72,12.429H31.98Zm-3.865,4.4L13.9,16.177,3.838,2.083H8.172L16.3,13.462l1.262,1.773L28.12,30.025H23.786Zm0,0" transform="translate(6.313 6.281)" fill="#0f1419" />
                                            </svg> : <img src={`${CloudUrl}/network/${activeData.networkId}.png`} />}
                                        </div>
                                        : <NetworkCardComponent network={netwrokd} font={newApi.wall.ThemeRule} />
                                    }
                                    <FontFamilyForPost item={activeData} newApi={newApi} personalization={newApi.wall.Personalization} themeRule={newApi.wall.ThemeRule} network={netwrokd} signageTheme={newApi.wall.Personalization.signageTheme} ownerID={newApi.wall.Wall.owner} />
                                    <h4><AuthorDetails item={activeData} personalization={newApi.wall.Personalization} font={newApi.wall.ThemeRule} backgroundColor={netwrokd.color} network={netwrokd} /></h4>
                                </div>

                            </div>
                        </div>


                    </div>
                </div >

            </>
        )
    }
}
export default Posts;